"use client"

import { Bus } from './SimulationDashboard'
import { CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import { Badge } from '@/components/ui/badge'
import { Progress } from '@/components/ui/progress'
import { Alert, AlertDescription } from '@/components/ui/alert'

interface BusInfoPanelProps {
  buses: Bus[]
  simulationTime: number
  totalPassengers: number
  error: string | null
}

export function BusInfoPanel({ buses, simulationTime, totalPassengers, error }: BusInfoPanelProps) {
  const formatTime = (seconds: number) => {
    const minutes = Math.floor(seconds / 60)
    const remainingSeconds = seconds % 60
    return `${minutes.toString().padStart(2, '0')}:${remainingSeconds.toString().padStart(2, '0')}`
  }

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'running': return 'bg-green-500'
      case 'stopped': return 'bg-red-500'
      case 'maintenance': return 'bg-yellow-500'
      default: return 'bg-gray-500'
    }
  }

  const getStatusText = (status: string) => {
    switch (status) {
      case 'running': return 'En Servicio'
      case 'stopped': return 'Detenido'
      case 'maintenance': return 'Mantenimiento'
      default: return 'Desconocido'
    }
  }

  const getRouteColor = (route: string) => {
    if (route.includes('Centro - Bocagrande')) return 'bg-blue-100 text-blue-800 border-blue-200'
    if (route.includes('Manga - Castillogrande')) return 'bg-green-100 text-green-800 border-green-200'
    if (route.includes('Pie de la Popa - Bazurto')) return 'bg-red-100 text-red-800 border-red-200'
    return 'bg-gray-100 text-gray-800 border-gray-200'
  }

  const totalCapacity = buses.reduce((sum, bus) => sum + bus.capacity, 0)
  const averageOccupancy = totalCapacity > 0 ? (totalPassengers / totalCapacity) * 100 : 0

  return (
    <div className="h-full">
      <CardHeader>
        <CardTitle className="text-lg">Informaci√≥n del Sistema</CardTitle>
      </CardHeader>
      
      <CardContent className="space-y-4 h-[calc(100%-80px)] overflow-y-auto">
        {/* Error Alert */}
        {error && (
          <Alert variant="destructive">
            <AlertDescription>{error}</AlertDescription>
          </Alert>
        )}

        {/* Estad√≠sticas generales */}
        <div className="bg-gray-50 p-3 rounded-lg">
          <div className="text-sm font-medium text-gray-700 mb-2">Estad√≠sticas Generales</div>
          <div className="grid grid-cols-2 gap-2 text-xs">
            <div>
              <div className="text-gray-500">Tiempo de Simulaci√≥n</div>
              <div className="font-semibold text-blue-600">{formatTime(simulationTime)}</div>
            </div>
            <div>
              <div className="text-gray-500">Buses Activos</div>
              <div className="font-semibold text-green-600">{buses.filter(b => b.status === 'running').length}</div>
            </div>
            <div>
              <div className="text-gray-500">Total Pasajeros</div>
              <div className="font-semibold text-purple-600">{totalPassengers}</div>
            </div>
            <div>
              <div className="text-gray-500">Ocupaci√≥n Promedio</div>
              <div className="font-semibold text-orange-600">{averageOccupancy.toFixed(1)}%</div>
            </div>
          </div>
        </div>

        {/* Informaci√≥n de cada bus */}
        <div className="space-y-3">
          <div className="text-sm font-medium text-gray-700">Buses en Operaci√≥n</div>
          
          {buses.map((bus) => (
            <div key={bus.id} className="border rounded-lg p-3 bg-white">
              {/* Header del bus */}
              <div className="flex items-center justify-between mb-2">
                <div className="flex items-center space-x-2">
                  <div className={`w-3 h-3 rounded-full ${getStatusColor(bus.status)}`}></div>
                  <span className="font-semibold text-sm">{bus.id}</span>
                </div>
                <Badge variant="outline" className="text-xs">
                  {getStatusText(bus.status)}
                </Badge>
              </div>

              {/* Ruta */}
              <div className={`text-xs px-2 py-1 rounded border mb-2 ${getRouteColor(bus.route)}`}>
                {bus.route}
              </div>

              {/* Informaci√≥n de paradas */}
              <div className="text-xs space-y-1 mb-2">
                <div className="flex justify-between">
                  <span className="text-gray-500">Parada Actual:</span>
                  <span className="font-medium">{bus.currentStop}</span>
                </div>
                <div className="flex justify-between">
                  <span className="text-gray-500">Pr√≥xima Parada:</span>
                  <span className="font-medium">{bus.nextStop}</span>
                </div>
                <div className="flex justify-between">
                  <span className="text-gray-500">Velocidad:</span>
                  <span className="font-medium">{bus.speed} km/h</span>
                </div>
              </div>

              {/* Ocupaci√≥n del bus */}
              <div className="space-y-1">
                <div className="flex justify-between text-xs">
                  <span className="text-gray-500">Ocupaci√≥n</span>
                  <span className="font-medium">{bus.passengers}/{bus.capacity}</span>
                </div>
                <Progress 
                  value={(bus.passengers / bus.capacity) * 100} 
                  className="h-2"
                />
                <div className="text-xs text-gray-500 text-center">
                  {((bus.passengers / bus.capacity) * 100).toFixed(1)}% ocupado
                </div>
              </div>
            </div>
          ))}
        </div>

        {/* Informaci√≥n adicional sobre Cartagena */}
        <div className="bg-blue-50 p-3 rounded-lg border border-blue-200">
          <div className="text-sm font-medium text-blue-800 mb-2">Sobre el Transporte en Cartagena</div>
          <div className="text-xs text-blue-700 space-y-1">
            <div>‚Ä¢ Sistema de transporte p√∫blico urbano</div>
            <div>‚Ä¢ Conecta principales sectores de la ciudad</div>
            <div>‚Ä¢ Rutas que incluyen el Centro Hist√≥rico</div>
            <div>‚Ä¢ Servicio hacia zonas tur√≠sticas y residenciales</div>
            <div>‚Ä¢ Integraci√≥n con diferentes barrios de Cartagena</div>
          </div>
        </div>

        {/* Rutas principales */}
        <div className="bg-gray-50 p-3 rounded-lg">
          <div className="text-sm font-medium text-gray-700 mb-2">Rutas Principales</div>
          <div className="space-y-2 text-xs">
            <div className="flex items-center space-x-2">
              <div className="w-3 h-2 bg-blue-500 rounded"></div>
              <span>Centro Hist√≥rico ‚Üî Bocagrande</span>
            </div>
            <div className="flex items-center space-x-2">
              <div className="w-3 h-2 bg-green-500 rounded"></div>
              <span>Manga ‚Üî Castillogrande</span>
            </div>
            <div className="flex items-center space-x-2">
              <div className="w-3 h-2 bg-red-500 rounded"></div>
              <span>Pie de la Popa ‚Üî Bazurto</span>
            </div>
          </div>
        </div>
      </CardContent>
    </div>
  )
}
