"use client"

import { Button } from '@/components/ui/button'
import { Slider } from '@/components/ui/slider'
import { CardContent, CardHeader, CardTitle } from '@/components/ui/card'

interface SimulationControlsProps {
  isRunning: boolean
  isPaused: boolean
  speed: number
  onStart: () => void
  onPause: () => void
  onStop: () => void
  onReset: () => void
  onSpeedChange: (speed: number) => void
}

export function SimulationControls({
  isRunning,
  isPaused,
  speed,
  onStart,
  onPause,
  onStop,
  onReset,
  onSpeedChange
}: SimulationControlsProps) {
  const handleSpeedChange = (value: number[]) => {
    onSpeedChange(value[0])
  }

  return (
    <div>
      <CardHeader>
        <CardTitle className="text-lg">Controles de Simulaci√≥n</CardTitle>
      </CardHeader>
      
      <CardContent className="space-y-4">
        {/* Botones principales */}
        <div className="grid grid-cols-2 gap-2">
          <Button
            onClick={onStart}
            disabled={isRunning && !isPaused}
            variant={isRunning && !isPaused ? "secondary" : "default"}
            className="w-full"
          >
            {isRunning && !isPaused ? "Ejecutando" : "Iniciar"}
          </Button>
          
          <Button
            onClick={onPause}
            disabled={!isRunning}
            variant={isPaused ? "default" : "secondary"}
            className="w-full"
          >
            {isPaused ? "Reanudar" : "Pausar"}
          </Button>
        </div>

        <div className="grid grid-cols-2 gap-2">
          <Button
            onClick={onStop}
            disabled={!isRunning}
            variant="destructive"
            className="w-full"
          >
            Detener
          </Button>
          
          <Button
            onClick={onReset}
            variant="outline"
            className="w-full"
          >
            Reiniciar
          </Button>
        </div>

        {/* Control de velocidad */}
        <div className="space-y-3">
          <div className="flex justify-between items-center">
            <label className="text-sm font-medium text-gray-700">
              Velocidad de Simulaci√≥n
            </label>
            <span className="text-sm text-gray-500">
              {speed}x
            </span>
          </div>
          
          <Slider
            value={[speed]}
            onValueChange={handleSpeedChange}
            min={0.5}
            max={5}
            step={0.5}
            className="w-full"
          />
          
          <div className="flex justify-between text-xs text-gray-500">
            <span>0.5x</span>
            <span>Normal</span>
            <span>5x</span>
          </div>
        </div>

        {/* Estado actual */}
        <div className="pt-4 border-t">
          <div className="text-sm">
            <div className="flex justify-between items-center mb-2">
              <span className="text-gray-600">Estado:</span>
              <span className={`font-medium ${
                isRunning 
                  ? isPaused 
                    ? 'text-yellow-600' 
                    : 'text-green-600'
                  : 'text-gray-600'
              }`}>
                {isRunning 
                  ? isPaused 
                    ? 'Pausado' 
                    : 'Ejecutando'
                  : 'Detenido'
                }
              </span>
            </div>
            
            <div className="flex justify-between items-center">
              <span className="text-gray-600">Velocidad:</span>
              <span className="font-medium text-blue-600">
                {speed}x
              </span>
            </div>
          </div>
        </div>

        {/* Instrucciones */}
        <div className="pt-4 border-t">
          <div className="text-xs text-gray-500 space-y-1">
            <div className="font-medium mb-2">Instrucciones:</div>
            <div>‚Ä¢ Iniciar: Comienza la simulaci√≥n</div>
            <div>‚Ä¢ Pausar: Pausa/reanuda temporalmente</div>
            <div>‚Ä¢ Detener: Finaliza la simulaci√≥n</div>
            <div>‚Ä¢ Reiniciar: Vuelve al estado inicial</div>
            <div>‚Ä¢ Velocidad: Controla la rapidez (0.5x - 5x)</div>
          </div>
        </div>
      </CardContent>
    </div>
  )
}
