"use client"

import { useState, useEffect } from 'react'
import { SimulationMap } from './SimulationMap'
import { SimulationControls } from './SimulationControls'
import { BusInfoPanel } from './BusInfoPanel'
import { Card } from '@/components/ui/card'

export interface Bus {
  id: string
  route: string
  position: { x: number; y: number }
  currentStop: string
  nextStop: string
  passengers: number
  capacity: number
  speed: number
  status: 'running' | 'stopped' | 'maintenance'
}

export interface SimulationState {
  isRunning: boolean
  isPaused: boolean
  simulationTime: number
  speed: number
  buses: Bus[]
  totalPassengers: number
  error: string | null
}

const initialBuses: Bus[] = [
  {
    id: 'BUS-001',
    route: 'Ruta Centro - Bocagrande',
    position: { x: 10, y: 50 },
    currentStop: 'Centro Hist√≥rico',
    nextStop: 'Plaza de la Aduana',
    passengers: 25,
    capacity: 40,
    speed: 30,
    status: 'running'
  },
  {
    id: 'BUS-002',
    route: 'Ruta Manga - Castillogrande',
    position: { x: 30, y: 30 },
    currentStop: 'Manga',
    nextStop: 'Puente Rom√°n',
    passengers: 18,
    capacity: 40,
    speed: 25,
    status: 'running'
  },
  {
    id: 'BUS-003',
    route: 'Ruta Pie de la Popa - Bazurto',
    position: { x: 70, y: 80 },
    currentStop: 'Pie de la Popa',
    nextStop: 'Mercado de Bazurto',
    passengers: 32,
    capacity: 45,
    speed: 20,
    status: 'running'
  }
]

export function SimulationDashboard() {
  const [simulation, setSimulation] = useState<SimulationState>({
    isRunning: false,
    isPaused: false,
    simulationTime: 0,
    speed: 1,
    buses: initialBuses,
    totalPassengers: initialBuses.reduce((sum, bus) => sum + bus.passengers, 0),
    error: null
  })

  useEffect(() => {
    let interval: NodeJS.Timeout | null = null

    if (simulation.isRunning && !simulation.isPaused) {
      interval = setInterval(() => {
        setSimulation(prev => {
          const newBuses = prev.buses.map(bus => {
            // Simular movimiento del bus
            const newX = (bus.position.x + (bus.speed * prev.speed * 0.1)) % 100
            const newY = bus.position.y + (Math.sin(newX * 0.1) * 2)
            
            // Simular cambios en pasajeros
            const passengerChange = Math.floor(Math.random() * 6) - 3 // -3 a +3
            const newPassengers = Math.max(0, Math.min(bus.capacity, bus.passengers + passengerChange))

            return {
              ...bus,
              position: { x: newX, y: Math.max(10, Math.min(90, newY)) },
              passengers: newPassengers
            }
          })

          return {
            ...prev,
            simulationTime: prev.simulationTime + 1,
            buses: newBuses,
            totalPassengers: newBuses.reduce((sum, bus) => sum + bus.passengers, 0)
          }
        })
      }, 1000 / simulation.speed)
    }

    return () => {
      if (interval) clearInterval(interval)
    }
  }, [simulation.isRunning, simulation.isPaused, simulation.speed])

  const handleStart = () => {
    setSimulation(prev => ({
      ...prev,
      isRunning: true,
      isPaused: false,
      error: null
    }))
  }

  const handlePause = () => {
    setSimulation(prev => ({
      ...prev,
      isPaused: !prev.isPaused
    }))
  }

  const handleStop = () => {
    setSimulation(prev => ({
      ...prev,
      isRunning: false,
      isPaused: false
    }))
  }

  const handleReset = () => {
    setSimulation({
      isRunning: false,
      isPaused: false,
      simulationTime: 0,
      speed: 1,
      buses: initialBuses,
      totalPassengers: initialBuses.reduce((sum, bus) => sum + bus.passengers, 0),
      error: null
    })
  }

  const handleSpeedChange = (newSpeed: number) => {
    setSimulation(prev => ({
      ...prev,
      speed: newSpeed
    }))
  }

  return (
    <div className="grid grid-cols-1 lg:grid-cols-4 gap-6 h-[calc(100vh-200px)]">
      {/* Mapa de simulaci√≥n - Columna principal */}
      <div className="lg:col-span-3">
        <Card className="h-full">
          <SimulationMap 
            buses={simulation.buses}
            isRunning={simulation.isRunning}
          />
        </Card>
      </div>

      {/* Panel lateral */}
      <div className="space-y-6">
        {/* Controles */}
        <Card>
          <SimulationControls
            isRunning={simulation.isRunning}
            isPaused={simulation.isPaused}
            speed={simulation.speed}
            onStart={handleStart}
            onPause={handlePause}
            onStop={handleStop}
            onReset={handleReset}
            onSpeedChange={handleSpeedChange}
          />
        </Card>

        {/* Informaci√≥n de buses */}
        <Card className="flex-1">
          <BusInfoPanel
            buses={simulation.buses}
            simulationTime={simulation.simulationTime}
            totalPassengers={simulation.totalPassengers}
            error={simulation.error}
          />
        </Card>
      </div>
    </div>
  )
}
